import React, { Component } from "react";
import {
  withGoogleMap,
  GoogleMap,
  withScriptjs,
  InfoWindow,
  Marker,
} from "react-google-maps";
import Geocode from "react-geocode";
import AutoComplete from "./AutoComplete";
const GoogleMapsAPI = "AIzaSyDtwWPRblwORncOCDiJpfnRN_DpZ4QJHi8";

Geocode.setApiKey(GoogleMapsAPI);
Geocode.enableDebug();

class Map extends Component {
  constructor(props) {
    super(props);
    this.state = {
      address: "",
      mapPosition: {
        lat: this.props.center.lat,
        lng: this.props.center.lng,
      },
      markerPosition: {
        lat: this.props.center.lat,
        lng: this.props.center.lng,
      },
    };
  }

  componentDidMount() {
    Geocode.fromLatLng(
      this.state.mapPosition.lat,
      this.state.mapPosition.lng
    ).then(
      (response) => {
        const address = response.results[0].formatted_address,
          addressArray = response.results[0].address_components;

        this.setState({
          address: address ? address : "",
        });
      },
      (error) => {
        console.error(error);
      }
    );
    // const script = document.createElement("script");

    // script.src = `https://maps.googleapis.com/maps/api/js?key=${GoogleMapsAPI}&libraries=places`;
    // script.async = true;

    // document.body.appendChild(script);
  }

  // shouldComponentUpdate(nextProps, nextState) {
  //   console.log("nextProps", nextProps);
  //   console.log("nextState", nextState);
  //   if (
  //     this.state.markerPosition.lat !== this.props.center.lat ||
  //     this.state.address !== nextState.address
  //   ) {
  //     return false;
  //   } else if (this.props.center.lat === nextProps.center.lat) {
  //     return false;
  //   }
  // }

  onChange = (event) => {
    this.setState({ [event.target.name]: event.target.value });
  };

  // onInfoWindowClose = (event) => {};

  onMarkerDragEnd = (event) => {
    let newLat = event.latLng.lat(),
      newLng = event.latLng.lng();
    this.setState({
      markerPosition: {
        lat: event.latLng.lat(),
        lng: event.latLng.lng(),
      },
      mapPosition: {
        lat: event.latLng.lat(),
        lng: event.latLng.lng(),
      },
    });

    this.props.setValue({
      address: "ABC",
      Coordinate: { lat: newLat, lng: newLng },
    });

    Geocode.fromLatLng(newLat, newLng).then(
      (response) => {
        const address = response.results[0].formatted_address,
          addressArray = response.results[0].address_components;
        // console.log(address, addressArray);
        // this.setState({
        //   address: address ? address : "",
        //   markerPosition: {
        //     lat: newLat,
        //     lng: newLng,
        //   },
        //   mapPosition: {
        //     lat: newLat,
        //     lng: newLng,
        //   },
        // });
      },
      (error) => {
        console.error(error);
      }
    );
  };

  onPlaceSelected = (place) => {
    const address = place.formatted_address,
      addressArray = place.address_components,
      latValue = place.geometry.location.lat(),
      lngValue = place.geometry.location.lng();
    // Set these values in the state.
    this.setState({
      address: address ? address : "",
      markerPosition: {
        lat: latValue,
        lng: lngValue,
      },
      mapPosition: {
        lat: latValue,
        lng: lngValue,
      },
    });
  };

  render() {
    const AsyncMap = withScriptjs(
      withGoogleMap((props) => (
        <GoogleMap
          google={this.props.google}
          zoom={10}
          center={{
            lat: this.state.mapPosition.lat,
            lng: this.state.mapPosition.lng,
          }}
        >
          <Marker
            google={this.props.google}
            draggable={true}
            onDragEnd={this.onMarkerDragEnd}
            position={{
              lat: this.state.markerPosition.lat,
              lng: this.state.markerPosition.lng,
            }}
          />
          <Marker />
        </GoogleMap>
      ))
    );
    let map;
    if (this.props.center.lat !== undefined) {
      map = (
        <div>
          {this.props.getAddress && (
            <AutoComplete getData={this.props.getAddress} />
          )}
          <AsyncMap
            googleMapURL={`https://maps.googleapis.com/maps/api/js?key=${GoogleMapsAPI}&libraries=places`}
            loadingElement={<div style={{ height: `100%` }} />}
            containerElement={<div style={{ height: this.props.height }} />}
            mapElement={<div style={{ height: `100%` }} />}
          />
        </div>
      );
    } else {
      map = <div style={{ height: this.props.height }} />;
    }
    return map;
  }
}
export default Map;
