import React, { useEffect, useState } from "react";
import { alpha, makeStyles, createStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import "../../Css/Home.css";
import MicSharpIcon from "@material-ui/icons/MicSharp";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import "react-multi-carousel/lib/styles.css";
import img1 from "../assest/map.png";
import img2 from "../assest/back121.jpg";
import Navbar from "../Home/Navbar";
import Footer from "./Footer";
import Slider from "./Slider";
import SearchIcon from "@material-ui/icons/Search";
import InputBase from "@material-ui/core/InputBase";
import { Paper } from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import CloseIcon from "@material-ui/icons/Close";
import { useHistory, useParams } from "react-router-dom";
import Map from "../Dashboard/common/Map";
import { TradingService } from "../../Service";
import Loader from "../Dashboard/common/Loader";

export default function Home() {
  const classes = useStyles();
  const history = useHistory();
  const param = useParams();
  const [tab1, setTab1] = useState(false);
  const [tab2, setTab2] = useState(true);
  const [tab3, setTab3] = useState(false);
  const [tab4, setTab4] = useState(false);
  const [details, setDetails] = useState({
    // location: {
    //   Coordinate: {
    //     lat: 21.1539,
    //     lng: 79.0831,
    //   },
    // },
    // img: img2,
    // title: "Giovanni",
    // description: "description",
  });

  useEffect(() => {
    getData();
  }, []);

  const getData = () => {
    TradingService.getDetailsId(param.id).then((res) => {
      if (res) {
        setDetails({
          location: res.location,
          img:
            "http://ec2-3-125-223-135.eu-central-1.compute.amazonaws.com:8080/uploads/" +
            res.Image.filename,
          title: res.oliver[0].title,
          description: res.oliver[0].description,
        });
      }
    });
  };
  return (
    <>
      {tab2 && (
        <>
          <div style={{ width: "100%", overflow: "hidden" }}>
            <div className={classes.grow}>
              <AppBar position="fixed" style={{ backgroundColor: "#fff" }}>
                <Toolbar>
                  <Typography className={classes.title2} variant="h6" noWrap>
                    OLIVER
                  </Typography>
                  <div className={classes.search}>
                    <div className={classes.searchIcon}>
                      <SearchIcon />
                    </div>
                    <InputBase
                      placeholder="Search…"
                      classes={{
                        root: classes.inputRoot,
                        input: classes.inputInput,
                      }}
                      inputProps={{ black: "search" }}
                    />
                  </div>
                  <div className={classes.grow} />
                  <div
                    className={classes.sectionDesktop1}
                    onClick={() => history.push("/add-poi")}
                  >
                    <Typography className={classes.title0} variant="h6" noWrap>
                      OLIVER STUDIO
                    </Typography>
                  </div>
                  <div className={classes.sectionDesktop}>
                    <Typography className={classes.title1} variant="h6" noWrap>
                      All Channel
                    </Typography>
                  </div>
                  <div className={classes.grow1} />
                  <div
                    className={classes.sectionDesktop2}
                    onClick={() => {
                      setTab3(true);
                      setTab2(false);
                    }}
                  >
                    <Typography className={classes.title3} variant="h6" noWrap>
                      Login
                    </Typography>
                  </div>
                </Toolbar>
              </AppBar>
            </div>
            <div className="none">
              <AppBar position="static" className={classes.head00}>
                <Toolbar>
                  <Typography className={classes.titlenav2} variant="h6" noWrap>
                    {details ? details.title : "..."}
                  </Typography>
                  <div className={classes.grow11} />
                  <div className={classes.sectionDesktopnav1}>
                    <Typography
                      className={classes.titlenav0}
                      variant="h6"
                      noWrap
                    >
                      Like
                    </Typography>
                  </div>
                </Toolbar>
              </AppBar>
            </div>
            <div className="home_00">
              <div className="home_01">
                <div className="home0">
                  <img
                    src={details.img ? details.img : img2}
                    alt="map"
                    style={{ width: "100%", height: "100%", borderRadius: 10 }}
                  />
                </div>
                <div className="home01">
                  <div className="icon0">
                    <MicSharpIcon className="icon1" style={{ width: 83 }} />
                  </div>
                  <div className="home011">
                    <text style={{ color: "#fff", fontWeight: "bold" }}>
                      PLAY OLIVER
                    </text>
                  </div>
                </div>
              </div>
              <div className="home_01">
                <div className="home1">
                  {details.location ? (
                    <Map list={[{ location: details.location }]} zoom={12} />
                  ) : (
                    <Loader h="300px" />
                  )}
                </div>
                <div className="home11">
                  <div className="home12">
                    <p className="home12_p">AUTHOR:</p>
                  </div>
                  <div className="home12_01">
                    <p className="home12_p1">OLIVER</p>
                  </div>
                </div>
              </div>
            </div>
            <div className={classes.root}></div>

            <AppBar position="static" className={classes.home3}>
              <Toolbar variant="dense">
                <IconButton
                  edge="start"
                  className={classes.menuButton}
                  color="inherit"
                  aria-label="menu"
                >
                  <MenuIcon />
                </IconButton>
                <Typography
                  variant="h8"
                  color="inherit"
                  className={classes.text}
                >
                  1 Like
                </Typography>
              </Toolbar>
              <Toolbar variant="dense">
                <IconButton
                  edge="start"
                  className={classes.menuButton}
                  color="inherit"
                  aria-label="menu"
                >
                  <MenuIcon />
                </IconButton>
                <Typography
                  variant="h8"
                  color="inherit"
                  className={classes.text}
                >
                  Discovery
                </Typography>
              </Toolbar>
              <div style={{ flexGrow: "0.98" }} />
              <Typography
                variant="h8"
                color="inherit"
                className={classes.text1}
              >
                Publication Date: 05/12/2019
              </Typography>
            </AppBar>
            <div className="home30">
              <h4>Description</h4>
              <div>
                {details.description}
                {/* <p>
                  San Giovanni Lupatoto is an Italian town in the province of
                  Verona in the Veneto region. The origin of the name is
                  somewhat uncertain and there are several hypotheses. While San
                  Giovanni was added following the establishment of the parish,
                  the term Lupatoto could derive from: &quot;lupum totum&quot;
                  which, could be a possible Latin name of a Roman camp during
                  the occupation of the territories where there were wolves
                  everywhere. In the eighteenth century several discoveries were
                  made which date back to the Iron Age by scholars. In the
                  hamlet of Raldon, at the Ca &#39;dei Frè locality, the remains
                  of various stilt houses were found. This was the southernmost
                  point of the municipality, located near the limits of the
                  ancient Veronese marsh. There is no longer any trace of these
                  findings as they were lost during the Napoleonic period.
                  Necropolises dating back to the Celts have been found in the
                  Lupatotine area in Pontoncello. During the Napoleonic period
                  the history of the commune is briefly intertwined with that of
                  the Count of Lilac, future King of France with the name of
                  Louis eighteenth. These in 1794, fleeing from the threat of
                  the French revolutionary troops, found refuge with the Counts
                  Gazzola and stayed in their properties in Verona and Palazzina
                  Sant&#39;Andrea. From this exile the Count continued his
                  political activity through proclamations and alliances with
                  the Vendean rebels. When Napoleon Bonaparte&#39;s French army
                  arrives near Verona, the Count flees and takes refuge first in
                  the Republic of Venice and then in Germany, finding refuge in
                  the Duke of Brunswick. The territory of San Giovanni Lupatoto
                  is also involved in the struggle between France and Austria.
                  In particular, the battle of the Magnano takes place on April
                  5, 1798. In 1927, in the middle of the fascist period, the
                  historic town of San Giovanni Lupatoto was suppressed and
                  merged into Verona. In 1932 the municipality was restored but
                  the hamlet of Palazzina Sant&#39;Andrea remained in Verona.
                  Some would like to merge the municipality of San Giovanni
                  Lupatoto again with the municipality of Verona.
                </p>
              */}
              </div>
            </div>
            <Slider label="Other OLIVER in this area" />
            <Slider label="You Might Like" />
            <div className="home4">
              <div className="home4_0">
                <div className="home4_01">
                  <div className="home4_02">
                    <p className="pppp">ABOUT OLIVER</p>
                  </div>
                </div>
                <div className="home4_01">
                  <div className="home4_02">
                    <p className="pppp">ABOUT US</p>
                  </div>
                </div>
                <div className="home4_01">
                  <div className="home4_02">
                    <p className="pppp">TELL</p>
                  </div>
                </div>
                <div className="home4_01">
                  <div className="home4_02">
                    <p className="pppp">4 BUSINESS</p>
                  </div>
                </div>
                <div className="home4_01">
                  <div className="home4_02">
                    <p className="pppp">BLOG</p>
                  </div>
                </div>
                <div className="home4_01">
                  <div className="home4_02">
                    <p className="pppp">FAQ</p>
                  </div>
                </div>
                <div className="home4_01">
                  <div className="home4_02">
                    <p className="pppp">PRIVACY</p>
                  </div>
                </div>
                <div className="home4_01">
                  <div className="home4_02">
                    <p className="pppp">USE OF COOKIES</p>
                  </div>
                </div>
                <div className="home4_01">
                  <div className="home4_02">
                    <p className="pppp">ToS</p>
                  </div>
                </div>
              </div>
            </div>
            <Footer />
          </div>
        </>
      )}
      {tab1 && (
        <div style={{ width: "100%", height: "100%", backgroundColor: "#fff" }}>
          <div className="tab1">
            <div style={{ width: "100%" }}>
              <div
                onClick={() => {
                  setTab2(true);
                  setTab1(false);
                }}
                style={{ float: "right", width: "8%", cursor: "pointer" }}
              >
                <CloseIcon />
              </div>
            </div>
            <Typography gutterBottom align="center" className={classes.head01}>
              Login
            </Typography>
            <Typography gutterBottom align="center" className={classes.head02}>
              Login or register to OLIVER to unlock all the functionalities
            </Typography>
            <Typography gutterBottom align="center" className={classes.head03}>
              LOGIN WITH FACEBOOK
            </Typography>
            <Typography
              gutterBottom
              align="center"
              className={classes.head03}
              style={{ color: "#000", background: "#fff", marginTop: 20 }}
            >
              LOGIN WITH GOOGLE
            </Typography>
            <Typography
              gutterBottom
              align="center"
              className={classes.head03}
              style={{ background: "#000", marginTop: 20 }}
            >
              Sgn in With Apple
            </Typography>
            <Typography
              onClick={() => {
                setTab3(true);
                setTab1(false);
                setTab4(false);
              }}
              gutterBottom
              align="center"
              className={classes.head03}
              style={{ color: "green", background: "#fff", marginTop: 20 }}
            >
              LOGIN WITH CREDENTIALS
            </Typography>
          </div>
        </div>
      )}
      {tab3 && (
        <div style={{ width: "100%", height: "100%", backgroundColor: "#fff" }}>
          <div className="tab1">
            <div style={{ width: "100%" }}>
              <div style={{ float: "left", width: "92%" }}>
                <ArrowBackIcon
                  style={{ cursor: "pointer" }}
                  onClick={() => {
                    setTab3(false);
                    setTab1(false);
                    setTab2(true);
                  }}
                />
              </div>
              <div style={{ float: "left", width: "8%" }}>
                <CloseIcon
                  style={{ cursor: "pointer" }}
                  onClick={() => {
                    setTab3(false);
                    setTab1(false);
                    setTab2(true);
                  }}
                />
              </div>
            </div>

            <Typography gutterBottom align="center" className={classes.head01}>
              Login
            </Typography>
            <Typography gutterBottom align="center" className={classes.head02}>
              Insert your credentials
            </Typography>
            <TextField
              id="fullName"
              label="Email"
              type="Email"
              // value={allVar.fullName}
              // onChange={(e) => handleChange(e)}
              name="fullName"
              fullWidth
              variant="outlined"
              placeholder="Enter your Email"
              className={classes.textField}
            />
            <TextField
              id="email"
              label="Password"
              type="Password"
              // value={allVar.email}
              // onChange={(e) => handleChange(e)}
              name="Password"
              fullWidth
              variant="outlined"
              placeholder="Enter your Password"
              className={classes.textField}
            />
            <Typography
              onClick={() => history.push("/home2")}
              gutterBottom
              align="center"
              className={classes.head03}
              style={{ color: "#fff", background: "#14e0149c", marginTop: 20 }}
            >
              LOGIN
            </Typography>
            <Typography
              gutterBottom
              align="center"
              className={classes.head02}
              onClick={() => {
                setTab3(false);
                setTab1(false);
                setTab2(false);
                setTab4(true);
              }}
              style={{ cursor: "pointer" }}
            >
              Didn't have an account?
            </Typography>
          </div>
        </div>
      )}

      {tab4 && (
        <div style={{ width: "100%", height: "100%", backgroundColor: "#fff" }}>
          <div className="tab1">
            <div style={{ width: "100%" }}>
              <div style={{ float: "left", width: "92%" }}>
                <ArrowBackIcon
                  style={{ cursor: "pointer" }}
                  onClick={() => {
                    setTab3(true);
                    setTab1(false);
                    setTab2(false);
                    setTab4(false);
                  }}
                />
              </div>
              <div style={{ float: "left", width: "8%" }}>
                <CloseIcon
                  style={{ cursor: "pointer" }}
                  onClick={() => {
                    setTab3(false);
                    setTab1(false);
                    setTab2(true);
                    setTab4(false);
                  }}
                />
              </div>
            </div>

            <Typography gutterBottom align="center" className={classes.head01}>
              Signup
            </Typography>
            <Typography gutterBottom align="center" className={classes.head02}>
              Insert your details
            </Typography>
            <TextField
              id="fullName"
              label="Name"
              type="text"
              // value={allVar.fullName}
              // onChange={(e) => handleChange(e)}
              name="fullName"
              fullWidth
              variant="outlined"
              placeholder="Enter your name"
              className={classes.textField}
            />
            <TextField
              id="fullName"
              label="Email"
              type="Email"
              // value={allVar.fullName}
              // onChange={(e) => handleChange(e)}
              name="fullName"
              fullWidth
              variant="outlined"
              placeholder="Enter your Email"
              className={classes.textField}
            />

            <TextField
              id="email"
              label="Password"
              type="Password"
              // value={allVar.email}
              // onChange={(e) => handleChange(e)}
              name="email"
              fullWidth
              variant="outlined"
              placeholder="Enter your Password"
              className={classes.textField}
            />
            <TextField
              id="Contact"
              label="Contact"
              type="Email"
              // value={allVar.fullName}
              // onChange={(e) => handleChange(e)}
              name="Contact"
              fullWidth
              variant="outlined"
              placeholder="Enter your Contact No."
              className={classes.textField}
            />
            <Typography
              onClick={() => history.push("/home2")}
              gutterBottom
              align="center"
              className={classes.head03}
              style={{ color: "#fff", background: "#14e0149c", marginTop: 20 }}
            >
              LOGIN
            </Typography>
          </div>
        </div>
      )}
    </>
  );
}

const useStyles = makeStyles((theme) =>
  createStyles({
    head00: {
      backgroundColor: "#fff",
      boxShadow: "none",
      width: "95%",
      marginBottom: 10,
      marginLeft: 0,
      [theme.breakpoints.up("sm")]: {
        marginLeft: 30,
      },
    },
    textField: {
      marginTop: 25,
    },
    head01: {
      fontSize: 20,
      fontWeight: "bold",
      marginTop: 50,
    },
    head02: {
      fontSize: 15,
    },
    head03: {
      fontSize: 15,
      padding: 6,
      borderRadius: 50,
      background: "cornflowerblue",
      color: "#fff",
      width: "75%",
      marginLeft: "11%",
      marginTop: 32,
      boxShadow: "inherit",
      cursor: "pointer",
    },
    paper: {
      margin: "auto",
      width: 500,
      borderRadius: 15,
      padding: 20,
      marginTop: 100,
    },
    grow: {
      flexGrow: 3.5,
      height: 60,
      marginTop: 20,
    },
    grow11: {
      flexGrow: 0.6,
    },
    titlenav2: {
      color: "#000",
      marginTop: 20,
      fontSize: 29,
      fontWeight: 700,
    },
    titlenav0: {
      color: "#8bc34a",
      textAlign: "center",
      fontSize: 14,
      fontWeight: "bold",
    },

    sectionDesktopnav1: {
      width: 72,
      border: "2px solid #8bc34a",
      borderRadius: 50,
      padding: 9,
      marginTop: 15,
    },
    root: {
      flexGrow: 1,
    },
    menuButton: {
      marginRight: theme.spacing(1),
    },
    text: {
      color: "#000",
      fontWeight: "bold",
    },
    home3: {
      marginTop: 20,
      flexDirection: "row",
      backgroundColor: "lightgrey",
      boxShadow: "none",
      width: "91%",
      display: "none",
      marginLeft: 15,
      borderRadius: 6,
      [theme.breakpoints.up("sm")]: {
        marginLeft: 58,
      },
    },

    text1: {
      marginTop: 15,
      color: "#000",
      fontWeight: "bold",
    },
    grow: {
      flexGrow: 3.5,
      height: 90,
    },
    grow1: {
      flexGrow: 0.1,
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    title2: {
      color: "#000",
      display: "none",
      [theme.breakpoints.up("sm")]: {
        display: "block",
      },
    },
    title1: {
      color: "#fff",
      textAlign: "center",
      fontSize: 14,
      fontWeight: "bold",
    },
    title0: {
      color: "#8bc34a",
      textAlign: "center",
      fontSize: 14,
      fontWeight: "bold",
    },
    title3: {
      color: "#000",
      textAlign: "center",
      fontSize: 14,
      fontWeight: "bold",
      cursor: "pointer",
    },
    search: {
      position: "relative",
      borderRadius: theme.shape.borderRadius,
      backgroundColor: "rgb(224 224 224)",
      marginRight: theme.spacing(2),
      marginLeft: 0,
      display: "none",
      width: "100%",
      [theme.breakpoints.up("sm")]: {
        marginLeft: theme.spacing(3),
        width: "auto",
      },
    },
    searchIcon: {
      padding: theme.spacing(0, 2),
      height: "100%",
      position: "absolute",
      pointerEvents: "none",
      display: "flex",
      alignItems: "center",
      display: "none",
      justifyContent: "center",
      color: "#000",
      [theme.breakpoints.up("sm")]: {
        display: "block",
      },
    },
    inputRoot: {
      color: "inherit",
    },
    inputInput: {
      padding: theme.spacing(1, 1, 1, 0),
      // vertical padding + font size from searchIcon
      paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
      transition: theme.transitions.create("width"),
      width: "100%",
      [theme.breakpoints.up("md")]: {
        width: "20ch",
      },
    },
    sectionDesktop: {
      width: 162,
      border: "2px solid #8bc34a",
      borderRadius: 50,
      padding: 9,
      margin: 10,
      backgroundColor: "#8bc34a",
    },
    sectionDesktop1: {
      width: 162,
      border: "2px solid #8bc34a",
      borderRadius: 50,
      padding: 9,
    },
    sectionDesktop2: {
      width: 80,
      border: "0.5px solid lightgrey",
      borderRadius: 50,
      padding: 6,
    },
    sectionMobile: {
      display: "flex",
      [theme.breakpoints.up("md")]: {
        display: "none",
      },
    },
  })
);
