import { RequestHandler } from "./index";

export const test = async (postData) => {
  const res = await RequestHandler.get("get", postData);
  return res;
};

export const uploadFormInput = async (postData) => {
  const res = await RequestHandler.post("/FormInput", postData);
  return res;
};

export const TexttoSpeech = async (postData) => {
  const res = await RequestHandler.post("/text_to_speech", postData);
  return res;
};

export const uploadImage = async (postData) => {
  const res = await RequestHandler.post("/uploader", postData);
  return res;
};

export const getdetails = async () => {
  const res = await RequestHandler.get("/getdetails");
  return res;
};

export const getDetailsId = async (id) => {
  const res = await RequestHandler.get("/getdetailsbyid/" + id);
  return res;
};

export const getUploads = async (id) => {
  const res = await RequestHandler.get("/uploads/" + id);
  return res;
};
