import { BrowserRouter, Route, Switch } from "react-router-dom";
import Homes from "./components/Home/Home";
import Home2 from "./components/MainScreen/Mainscreen";
import Dashboard from "./components/Dashboard/dashboard";
import "./App.css";
function App() {
  return (
    <>
      <BrowserRouter>
        <Switch>
          <Route exact path="/">
            <Home2 />
          </Route>
          <Route exact path="/detail/:id">
            <Homes />
          </Route>
          <Route exact path="/add-poi">
            <Dashboard />
          </Route>
        </Switch>
      </BrowserRouter>
    </>
  );
}

export default App;
