import React from "react";
import { SidebarData } from "./SidebarData";
import "./dashboard.css"
function Sidebar() {
  return (
    <>
      <div className="dashboard0">
        <div className="dashboard01">
          <div className="dashboard02">
            <img src="" alt="" />
          </div>
          <div className="dashboard03"></div>
        </div>
        <ul className="dashboard-ul">
          {SidebarData.map((items, key) => {
            return (
              <>
                <li
                  key={key}
                  className="dashboard-li"
                  id={
                    window.location.pathname === items.link
                      ? "active"
                      : "nav" + key
                  }
                  onClick={() => {
                    window.location.pathname = items.link;
                  }}
                >
                  <div className="dashboard02">{items.Icon}</div>
                  <div className="dashboard03">{items.title}</div>
                </li>
              </>
            );
          })}
        </ul>
      </div>
    </>
  );
}
export default Sidebar;
