import Sidebar from "../common/Sidebar";
import React, { useEffect, useState, useRef } from "react";
import { makeStyles, createStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import Badge from "@material-ui/core/Badge";
import NotificationsIcon from "@material-ui/icons/Notifications";
import Map from "../common/Maps";
import NextLevelForm from "./NextLevelForm";
import LangList from "lang-list";
import Select from "react-select";
import { Close, Send } from "@material-ui/icons";
import CircularProgress from "@material-ui/core/CircularProgress";

import { TradingService } from "../../../Service";
import SimpleReactValidator from "simple-react-validator";
import { useHistory } from "react-router-dom";
import { keyword } from "./LangKeyword";

export default function PrimarySearchAppBar(props) {
  const classes = useStyles();
  const history = useHistory();
  const [languagList] = useState(
    LangList.getList({
      supportedLangs: ["en", "de", "it", "ro", "hi", "fr", "es"],
    }).map((item) => ({
      value: item.int,
      label: item.int,
      data: item,
    }))
  );
  const [languagValue, setLanguagValue] = useState();
  const [, forceUpdate] = useState("");
  const validator = useRef(
    new SimpleReactValidator({ autoForceUpdate: { forceUpdate: forceUpdate } })
  );
  const [formError, setFormError] = useState(false);
  const [calling, setCalling] = useState(false);

  const [formValue, setFormValue] = useState({
    address: "",
    Coordinate: {
      lat: 18.5204,
      lng: 73.8567,
    },
    Channel: "",
    Image: "",
    Publication_date: "",
    Expiration_date: "",
    Action_range: "",
    Oliver: [],
  });

  const onSelectLang = (e, f) => {
    setLanguagValue(e);
    let newArray = [...formValue.Oliver];
    if (f.removedValue) {
      let ind = newArray.findIndex(
        (item) => item.select_language === f.removedValue.label
      );
      newArray.splice(ind, 1);
    } else {
      newArray.push({
        select_language: f.option.label,
        title: "",
        description: "",
        link: "",
        file: "",
        heading: keyword[f.option.data.code],
      });
    }
    setFormValue({ ...formValue, Oliver: newArray });
  };

  const onChangeEvent = (e, act) => {
    let newArray = [...formValue.Oliver];
    newArray.splice(act, 1, {
      ...newArray[act],
      [e.target.name]:
        e.target.name === "file" ? e.target.files[0] : e.target.value,
    });
    setFormValue({ ...formValue, Oliver: newArray });
  };

  const onClickPublish = () => {
    let formData = new FormData();
    let str = formValue.Oliver.map((object) =>
      Object.values(object).filter((item) => item === "").length <= 0
        ? true
        : false
    );

    if (
      validator.current.allValid() &&
      JSON.stringify(str).search("false") < 0
    ) {
      setCalling(true);
      formData.append("file1", formValue.Image);
      formValue.Oliver.map((item, key) => {
        formData.append("file" + (key + 2), item.file);
      });

      TradingService.uploadImage(formData).then((res) => {
        if (res.status === "success") {
          let OliverArray = formValue.Oliver.map((item, key) => ({
            ...item,
            file: res.filename[key + 1],
          }));
          let postData = {
            ...formValue,
            Image: res.filename[0],
            Oliver: OliverArray,
            address: "ABCD",
          };
          TradingService.uploadFormInput(postData).then((res) => {
            if (res.status) {
              history.push("/");
            } else {
              setCalling(false);
              alert(res.message);
            }
          });
        } else {
          setCalling(false);
          alert(res.message);
        }
      });
    } else {
      setFormError(true);
      validator.current.showMessages();
    }
  };

  const getAddress = (add) => {
    console.log(add);
  };

  const onClickSpeech = (text) => {
    let postData = {
      text: text,
    };
    TradingService.TexttoSpeech(postData).then((res) => console.log(res));
  };

  return (
    <div className="d-flex">
      <Sidebar />
      <div className="maincontent flex-grow-1">
        <AppBar position="static" className={classes.head}>
          <Toolbar>
            <Typography className={classes.title} variant="h6" noWrap>
              DashBoard
            </Typography>
            <div className={classes.grow} />
            <div className={classes.sectionDesktop}>
              <IconButton
                aria-label="show 17 new notifications"
                color="inherit"
              >
                <Badge badgeContent={0} color="secondary">
                  <NotificationsIcon />
                </Badge>
              </IconButton>
            </div>
          </Toolbar>
        </AppBar>
        <div style={{ pointerEvents: calling ? "none" : "auto" }}>
          <div className="dashmain0">
            <h2 className="head00">Informazioni</h2>
          </div>
          {/* <div className="dashmain01"></div> */}
          <div className="row m-1">
            <div className="dashmain011 col-sm-12 col-md-12 col-lg-6">
              <div className="dashmain012 h-100">
                <p style={{ margin: "6px 0px 10px 0px" }}>Pick Location</p>
                <div className="dashmain013">
                  {/* {formValue.Coordinate && ( */}
                  <Map
                    google={props.google}
                    center={{ lat: 18.5204, lng: 73.8567 }}
                    height="300px"
                    setValue={(e) => setFormValue({ ...formValue, ...e })}
                    zoom={5}
                    getAddress={getAddress}
                  />
                  {/* )} */}
                </div>
                <div className="mt-2">
                  <label className="text-danger text-sm mx-2 pt-1">
                    <small className="text-dark">Channel</small>*
                  </label>
                  <input
                    className="form-control inputfield"
                    placeholder="type..."
                    type="text"
                    onChange={(e) =>
                      setFormValue({ ...formValue, Channel: e.target.value })
                    }
                  />
                  {validator.current.message(
                    "Channel",
                    formValue.Channel,
                    "required",
                    { className: "text-danger mb-2" }
                  )}
                </div>
              </div>
            </div>
            <div className="dashmain015 col-sm-12 col-md-12 col-lg-6">
              <div className="dashmain016 mt-3">
                <label className="text-danger text-sm mx-1">
                  <small className="text-dark">Image</small>*
                </label>
                <div className="text-center mb-5 mt-4">
                  {formValue.Image ? (
                    <img
                      className="dashmain017"
                      src={URL.createObjectURL(formValue.Image)}
                    />
                  ) : (
                    <label className="dashmain017 btn" htmlFor="myInput">
                      Select Image
                    </label>
                  )}
                  <input
                    id="myInput"
                    className="d-none"
                    type={"file"}
                    accept="image/*"
                    onChange={(e) =>
                      setFormValue({ ...formValue, Image: e.target.files[0] })
                    }
                  />
                  {validator.current.message(
                    "Image",
                    formValue.Image,
                    "required",
                    { className: "text-danger mb-1" }
                  )}
                  {formValue.Image && (
                    <div className="row">
                      <div className="text-center m-2 mt-3">
                        <button className="btn btn-secondary" htmlFor="myInput">
                          Change Image
                        </button>
                      </div>
                    </div>
                  )}
                </div>
                <div className="row">
                  <div className="col-sm-12 col-md-12 col-lg-6">
                    <label className="text-danger text-sm mb-1">
                      <small className="text-dark">Publication Date</small>*
                    </label>
                    <input
                      className="form-control inputfield"
                      type="Date"
                      min="2021-08-08"
                      onChange={(e) =>
                        setFormValue({
                          ...formValue,
                          Publication_date: e.target.value,
                        })
                      }
                    />
                    {validator.current.message(
                      "",
                      formValue.Publication_date,
                      "required",
                      { className: "text-danger mb-2" }
                    )}
                  </div>
                  <div className="col-sm-12 col-md-12 col-lg-6">
                    <label className="text-danger text-sm mb-1">
                      <small className="text-dark">Expiration Date</small>*
                    </label>
                    <input
                      className="form-control inputfield"
                      type="Date"
                      min="2021-08-08"
                      max={new Date()}
                      onChange={(e) =>
                        setFormValue({
                          ...formValue,
                          Expiration_date: e.target.value,
                        })
                      }
                    />
                    {validator.current.message(
                      "",
                      formValue.Expiration_date,
                      "required",
                      { className: "text-danger mb-2" }
                    )}
                  </div>
                  <div className="mt-2 col-12">
                    <label className="text-danger text-sm mb-1">
                      <small className="text-dark">Range of action</small>*
                    </label>
                    <input
                      className="form-control inputfield"
                      type="number"
                      onChange={(e) =>
                        setFormValue({
                          ...formValue,
                          Action_range: e.target.value,
                        })
                      }
                    />
                    {validator.current.message(
                      "Range",
                      formValue.Action_range,
                      "required",
                      { className: "text-danger mb-2" }
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="mx-2">
            <div className="mb-3">
              <label className="text-danger text-sm">
                <small className="text-dark">Select Language</small>*
              </label>
              <Select
                styles={{
                  valueContainer: (provided) => ({
                    ...provided,
                    height: "calc(2.15rem + 2px)",
                    overflowY: "visible",
                  }),
                  control: (provider) => ({
                    ...provider,
                    borderColor: "#ced4da",
                    boxShadow: "0 0 0 0px #f46a6a",
                  }),
                  placeholder: (provider) => ({
                    ...provider,
                    color: "#a0b0ca",
                  }),
                }}
                isMulti={true}
                value={languagValue}
                onChange={(e, f) => onSelectLang(e, f)}
                options={languagList}
              />
            </div>
            {formValue.Oliver && formValue.Oliver.length > 0 && (
              <NextLevelForm
                tabLang={formValue.Oliver}
                formError={formError}
                languagValue={languagValue}
                onChangeEvent={onChangeEvent}
                onClickSpeech={onClickSpeech}
              />
            )}
            {validator.current.message(
              "Language",
              formValue.Oliver[0]?.select_language,
              "required",
              { className: "text-danger mb-2" }
            )}
          </div>
          <div className="mb-5">
            <button
              type="button"
              className="btn btn-success m-1"
              onClick={() => onClickPublish()}
              disabled={calling}
            >
              Publish{" "}
              {calling ? (
                <CircularProgress style={{ width: "20px", height: "20px" }} />
              ) : (
                <Send />
              )}
            </button>
            <button type="button" className="btn btn-danger m-1">
              Cancel <Close />
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

const useStyles = makeStyles((theme) =>
  createStyles({
    head: {
      width: "100%",
    },
    grow: {
      flexGrow: 3.1,
    },
    menuButton: {},
    title: {
      [theme.breakpoints.up("sm")]: {
        display: "block",
      },
    },

    sectionDesktop: {
      [theme.breakpoints.up("md")]: {
        display: "flex",
      },
    },
  })
);
