import React from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import { Publish, RecordVoiceOver } from "@material-ui/icons";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      className="border mb-5"
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-prevent-tabpanel-${index}`}
      aria-labelledby={`scrollable-prevent-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `scrollable-prevent-tab-${index}`,
    "aria-controls": `scrollable-prevent-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: "100%",
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function ScrollableTabsButtonPrevent({
  onChangeEvent,
  tabLang,
  formError,
  onClickSpeech,
}) {
  const classNamees = useStyles();
  const [value, setValue] = React.useState(0);
  const [isShow, setShowSpeech] = React.useState(true);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div classNameName={classNamees.root}>
      {tabLang && (
        <>
          <AppBar color="transparent" position="static">
            <Tabs
              value={value}
              onChange={handleChange}
              variant="scrollable"
              aria-label="scrollable prevent tabs example"
            >
              {tabLang.length > 0 &&
                tabLang.map((item, key) => (
                  <Tab
                    key={key}
                    label={item.select_language}
                    aria-label={item.select_language}
                    {...a11yProps(key)}
                  />
                ))}
            </Tabs>
          </AppBar>
          {tabLang.length > 0 &&
            tabLang.map((item, key) => (
              <TabPanel value={value} index={key}>
                <>
                  <div className="mb-3">
                    <label className="form-label text-danger text-sm mb-1">
                      <small className="text-dark">{item.heading.Title}</small>*
                    </label>
                    <input
                      type="text"
                      className="form-control inputfield"
                      value={item.title}
                      name={"title"}
                      onChange={(e) => onChangeEvent(e, key)}
                    />
                    {formError && !item.title && (
                      <p className="text-danger py-1">{item.heading.error}</p>
                    )}
                  </div>
                  <div className="mb-3">
                    <label className="form-label text-danger text-sm mb-1">
                      <small className="text-dark">
                        {item.heading.Description}
                      </small>
                      *
                    </label>
                    <textarea
                      type="text"
                      className="form-control inputfield"
                      value={item.description}
                      name={"description"}
                      onChange={(e) => onChangeEvent(e, key)}
                    />
                    {formError && !item.description && (
                      <p className="text-danger py-1">{item.heading.error}</p>
                    )}
                  </div>
                  <div className="mb-3">
                    <label className="form-label text-danger text-sm mb-1">
                      <small className="text-dark">{item.heading.Link}</small>*
                    </label>
                    <input
                      className="form-control inputfield"
                      type="text"
                      aria-describedby="emailHelp"
                      value={item.link}
                      name={"link"}
                      onChange={(e) => onChangeEvent(e, key)}
                    />
                    {formError && !item.link && (
                      <p className="text-danger py-1">{item.heading.error}</p>
                    )}
                  </div>
                  <div>
                    <label className="form-label">
                      <b>{item.heading["Audio Console"]}</b>
                    </label>
                    <div className="row">
                      <div className="d-grid col-6">
                        <div className="w-100 pb-2">
                          <label
                            className="btn btn-primary btn-rounded w-100"
                            htmlFor="audioInput"
                          >
                            <Publish /> &nbsp; {item.heading["Upload Audio"]}
                          </label>
                          <input
                            id="audioInput"
                            className="d-none"
                            type="file"
                            accept="audio/*"
                            name="file"
                            onChange={(e) => onChangeEvent(e, key)}
                          />
                        </div>
                        <label className="w-100 text-center">
                          <b>OR</b>
                        </label>
                        <div className="pb-2">
                          <button
                            id="audioInput"
                            className="btn btn-success btn-rounded w-100"
                            type="file"
                            onClick={() => setShowSpeech(!isShow)}
                            // accept="audio/*"
                            // name="file"
                            // onChange={(e) => onChangeEvent(e, key)}
                          >
                            <RecordVoiceOver /> &nbsp;
                            {item.heading["text-to-speech"]}
                          </button>
                        </div>
                        {formError && !item.file && (
                          <p className="text-danger py-1">
                            {item.heading.error}
                          </p>
                        )}
                      </div>
                      <div className="col-6 text-center">
                        {isShow ? (
                          <>
                            <u>
                              {item.file.name
                                ? item.file.name
                                : "No file selected"}
                            </u>
                            <div className="audio-box">
                              <audio
                                controls
                                src={
                                  item.file
                                    ? URL.createObjectURL(item.file)
                                    : ""
                                }
                              ></audio>
                            </div>
                          </>
                        ) : (
                          <>
                            <div>
                              <textarea
                                className="w-100 h-100 border p-2"
                                col-5
                                placeholder="Write the written text of your loquis which will then be transformed into audio with synthesized voice."
                                value={item.text}
                                name="text"
                                onChange={(e) => onChangeEvent(e, key)}
                              ></textarea>
                            </div>
                            <button
                              className="btn btn-info btn-rounded w-100"
                              onClick={() => onClickSpeech(item.text)}
                            >
                              confirm
                            </button>
                          </>
                        )}
                      </div>
                    </div>
                  </div>
                </>
              </TabPanel>
            ))}
        </>
      )}
    </div>
  );
}
