import axios from "axios";

const API_ENDPOINT = process.env.REACT_APP_API_ENDPOINT;

const authHeader = {
  "Content-Type": "application/json",
  Accept: "application/json",
};

const client = axios.create({
  baseURL: API_ENDPOINT,
  withCredentials: false,
});

class RequestHandler {
  static get(path = "", postData) {
    let params = false;
    if (postData) {
      params = Object.keys(postData)
        .map((key) => key + "=" + postData[key])
        .join("&");
    }
    return client({
      method: "GET",
      url: params ? path + "?" + params : path,
      //   headers: authHeader,
    })
      .then((res) => {
        return res.data;
      })
      .catch((error) => {
        return false;
        // if (error.response) {
        //   // 500
        //   if (error.response.status && error.response.status === 401) {
        //     return {
        //       data: {
        //         status: false,
        //         message: error.response ? error.response.statusText : "",
        //       },
        //     };
        //   }
        //   return {
        //     data: {
        //       status: false,
        //       message: error.response ? error.response.statusText : "",
        //     },
        //   };
        // } else if (error.request) {
        //   return {
        //     data: {
        //       status: false,
        //     },
        //   };
        // } else {
        //   return error;
        // }
      });
  }

  static post(path = "", data = {}, optionalHeader = {}) {
    return client({
      method: "POST",
      url: path,
      data,
      //   headers: { ...authHeader, ...optionalHeader },
    })
      .then((res) => {
        return res.data;
      })
      .catch((error) => {
        if (error.response) {
          return error.response.data;
        } else if (error.request) {
          const request = {};
          request.status = false;
          request.message = "Something went wrong please try again";
          return request;
        } else {
          return error;
        }
      });
  }
}

export { RequestHandler };
