import React from "react";
import { alpha, makeStyles, createStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import InputBase from "@material-ui/core/InputBase";
import MenuIcon from "@material-ui/icons/Menu";
import SearchIcon from "@material-ui/icons/Search";
import { useHistory } from "react-router-dom";
const useStyles = makeStyles((theme) =>
  createStyles({
    grow: {
      flexGrow: 3.5,
      height: 90,
    },
    grow1: {
      flexGrow: 0.1,
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    title2: {
      color: "#000",
    },
    title1: {
      color: "#fff",
      textAlign: "center",
      fontSize: 14,
      fontWeight: "bold",
    },
    title0: {
      color: "#8bc34a",
      textAlign: "center",
      fontSize: 14,
      fontWeight: "bold",
      cursor: "pointer",
    },
    title3: {
      color: "#000",
      textAlign: "center",
      fontSize: 14,
      fontWeight: "bold",
    },
    search: {
      position: "relative",
      borderRadius: theme.shape.borderRadius,
      backgroundColor: "rgb(224 224 224)",
      marginRight: theme.spacing(2),
      marginLeft: 0,
      width: "100%",
      [theme.breakpoints.up("sm")]: {
        marginLeft: theme.spacing(3),
        width: "auto",
      },
    },
    searchIcon: {
      padding: theme.spacing(0, 2),
      height: "100%",
      position: "absolute",
      pointerEvents: "none",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      color: "#000",
    },
    inputRoot: {
      color: "inherit",
    },
    inputInput: {
      padding: theme.spacing(1, 1, 1, 0),
      // vertical padding + font size from searchIcon
      paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
      transition: theme.transitions.create("width"),
      width: "100%",
      [theme.breakpoints.up("md")]: {
        width: "20ch",
      },
    },
    sectionDesktop: {
      width: 162,
      border: "2px solid #8bc34a",
      borderRadius: 50,
      padding: 9,
      margin: 10,
      backgroundColor: "#8bc34a",
    },
    sectionDesktop1: {
      width: 162,
      border: "2px solid #8bc34a",
      borderRadius: 50,
      padding: 9,
    },
    sectionDesktop2: {
      width: 80,
      border: "0.5px solid lightgrey",
      borderRadius: 50,
      padding: 6,
    },
    sectionMobile: {
      display: "flex",
      [theme.breakpoints.up("md")]: {
        display: "none",
      },
    },
  })
);

export default function Navbar() {
  const classes = useStyles();
  const history = useHistory();

  return (
    <div className={classes.grow}>
      <AppBar position="fixed" style={{ backgroundColor: "#fff" }}>
        <Toolbar>
          <Typography className={classes.title2} variant="h6" noWrap>
            OLIVER
          </Typography>
          <div className={classes.search}>
            <div className={classes.searchIcon}>
              <SearchIcon />
            </div>
            <InputBase
              placeholder="Search…"
              classes={{
                root: classes.inputRoot,
                input: classes.inputInput,
              }}
              inputProps={{ black: "search" }}
            />
          </div>
          <div className={classes.grow} />
          <div className={classes.sectionDesktop1}>
            <Typography
              className={classes.title0}
              variant="h6"
              noWrap
              onClick={() => history.push("/add-poi")}
            >
              OLIVER STUDIO
            </Typography>
          </div>
          <div className={classes.sectionDesktop}>
            <Typography className={classes.title1} variant="h6" noWrap>
              All Channel
            </Typography>
          </div>
          <div className={classes.grow1} />
          <div className={classes.sectionDesktop2}>
            <Typography className={classes.title3} variant="h6" noWrap>
              Login
            </Typography>
          </div>
        </Toolbar>
      </AppBar>
    </div>
  );
}
